export default {
	data() {
		return {
			PageCur: '报事',
			imgList: [],
			modalName: null,
			radio: 'radio1',
			List1: [{
				name: '大安街道',
				flag: true,
			}, 
			{
				name: '凉高山街道',
				flag: true,
			}, 
			{
				name: '龙井街道',
				flag: true,
			},
			{
				name: '马冲口街道',
				flag: true,
			}],
			
			List2: [{
				name: '华大社区',
				flag: true,
			}, 
			{
				name: '大楻桶社区',
				flag: true,
			}, 
			{
				name: '广华社区',
				flag: true,
			},
			{
				name: '红苕地社区',
				flag: true,
			}],
			List3: [{
				name: '社区发展',
				flag: false,
			}, 
			{
				name: '组织建设',
				flag: false,
			}, 
			{
				name: '提升服务能力',
				flag: false,
			},
			{
				name: '其他',
				flag: false,
			}],
			List4: [{
				name: '华大小区',
				flag: true,
			}, 
			{
				name: '大楻桶小区',
				flag: true,
			}, 
			{
				name: '广华小区',
				flag: true,
			},
			{
				name: '红苕地小区',
				flag: true,
			}],
			
		}
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '照片',
				content: '确定要删除该照片吗？',
				cancelText: '取消',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		showModal1(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal1(e) {
			this.modalName = null
		},
		RadioChange1(e) {
			this.radio = e.detail.value
		},
		
		showModal2(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal2(e) {
			this.modalName = null
		},
		RadioChange2(e) {
			this.radio = e.detail.value
		},
		
		showModal3(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal3(e) {
			this.modalName = null
		},
		RadioChange3(e) {
			this.radio = e.detail.value
		},
		
		showModal4(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal4(e) {
			this.modalName = null
		},
		RadioChange4(e) {
			this.radio = e.detail.value
		},
	}
	
}
